@import url('https://fonts.googleapis.com/css?family=Titillium+Web:600&display=swap');

html {
  height: 100%;
  width: 100%;
  background-color: #f3f3f3;
}
.workplace {
  background-color: #f3f3f3;
}
.canvas .lr {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  height: 100%;
}
.canvas {
  height: 100%;
}
.m_img {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-template-rows: repeat(3,1fr);
  position: absolute;
  margin:0 auto;
  top:0;
  bottom:0;
  left:0;
  right:0;
  width:100%;
  height: 100%;

}
.m_img .m_content {
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 2;
  display: block;
  margin:0 auto;
  width: 320px;
  height: 450px;
  background-color: black;
  background-image: url('/source/mimg.jpg');
  background-size: cover;
  box-sizing: 0px 5px 5px;
  border: 3 px solid khaki;
  animation: slide_right2 .7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.m_img .desc {
  cursor: default;
  position: absolute;
  top:50%;
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 2;
  opacity: 0;
  animation: fadein_up .5s .7s linear forwards;
}
.fadein {
  animation: fadein .8s linear forwards;
}
.m_img p {
  line-height: 15px;
}
.workplace {
  color:white;
  width: 100%;
  height: 100%;
  color:black;
  background-color: #f3f3f3;
}
.underbar {
  text-decoration: underline;
}
.canvas .right {
}
.canvas .left {
  opacity: .8;
  background-image: url('/source/main.jpg');
  background-size: cover;
  opacity: 1;
  animation: slide_right .7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.disappear {
  display: hidden;
}
@keyframes slide_right {
  from {
    width: 0;
  }
  to{
    width: 100%;
  }
}
@keyframes slide_right2 {
  from {
    width: 0;
  }
  to{
    width: 320px;
  }
}
.small_desc {
  color:#f5f5f5;
  background-color: black;
}
.small_desc .s_desc {
  line-height: 10px;
  color:#d1d1d1;
  margin: 0 auto;
  width: 60%;
  padding-top: 150px;
  padding-bottom: 150px;
  font-family: 'Titillium Web', sans-serif;
  font-size:1.1em;
}
.canvas2 {
  color: #f5f5f5;
  text-align: center;
  background-image: url('/source/source.jpg');
  background-size: cover;
  width: 100%;
  height:100%;
}
.canvas3 {
  background-color: #f3f3f3;
  display: grid;
  height: 100%;
  grid-template-columns: repeat(2,1fr);
  width: 100%;
  min-height: 700px;
}
.canvas3 .left {
  height: 100%;
  display: grid;
}
.canvas3 .left .img_cover {
  position: relative;
  top:50%;
  transform:translateY(-50%);
  max-width: 600px;
  max-height: 700px;
  margin:0 auto;
}
.canvas3 .left img {
  width: 100%;
  height: 100%;
  display: inline-block;
  box-shadow: 0px 0px 50px -20px;
  transition: .5s;
}
.canvas3 .right {
  height: 100%;
}
.canvas3 .right .desc {
  position:relative;
  width: 80%;
  margin:0 auto;
  top:50%;
  transform: translateY(-50%);
  text-align: right;
  opacity:0;
}
.cvs_space {
  width: 100%;
  height: 100%;
}
.con_space {
  position: relative;
  width: 100%;
  height: 300px;
  background-color: rgb(255,255,255,0);
  z-index: -2;
}
.con_space .ctt {
  position: fixed;
  text-align: center;
  background-color: white;
  width: 100%;
  height: 300px;
  bottom:0;
  left:0;
  z-index: -1;
}
.con_space .ctt h1 {
  display: inline-block;
  color:#454545;
  font-size: 5em;
  mix-blend-mode: difference;
  margin-top: 100px;
}
.con_space .ctt h1:hover {
  color:white;
}
.canvas4 {
  background-color: #f3f3f3;
  display: grid;
  height: 100%;
  min-height: 700px;
  grid-template-columns: repeat(2,1fr);
  box-shadow: 0px 15px 20px -10px;
}
.canvas4 .right {
  height: 100%;
  display: grid;
}
.canvas4 .right .img_cover {
  position: relative;
  top:50%;
  transform:translateY(-50%);
  max-width: 400px;
  max-height: 700px;
  margin:0 auto;
}
.canvas4 .right img {
  width: 100%;
  height: 100%;
  display: inline-block;
  box-shadow: 0px 0px 50px -20px;
  border-radius: 15px;
}
.canvas4 .left {
  height: 100%;
}
.canvas4 .left .desc {
  position:relative;
  width: 80%;
  margin:0 auto;
  top:50%;
  opacity:0;
  transform: translateY(-50%);
}
.right .desc p {
  margin-right: 30px;
}
.left .desc p {
  margin-left: 30px;
}
p, h1, h2, h3 {
  cursor: default;
}
.fix {
  position: fixed;
  top:0;
  z-index:1;
}
.ctn {
  animation: ctn .5s linear forwards;
}
@keyframes ctn {
  from{
    width: 0%;
  }
  to{
    width: 100%;
  }
}
/* 가로길이 1107px, 780 */
@keyframes fadein_up {
  from {
    opacity:0;
    transform: translateY(30px);
  }
  to{
    opacity:1;
    transform: translateY(0px);
  }
}
@keyframes fadein {
  from {
    opacity:0;
  }
  to{
    opacity:1;
  }
}

@media ( max-width: 768px ) {
  .workplace {
    display: none;
  }
}
