body {
  margin: 0;

}

body, #root {
  margin:0;
  width: 100%;
  height: 100%;

}

.Typist {
  cursor: default;
}
.intro {
  background-color: #121212;
  width: 100%;
  height: 100%;
}
section {
  color:#f3f3f3;
  width: 95%;
  height: 100%;
  margin: 0 auto;
  background-color: #121212;
}
.cvs {
  position: relative;
  top:50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
  word-spacing: 3px;
  font-family: 'VT323', monospace;
  font-size: 3em;
  background-color: #121212;
}

.contact {
  width: 100%;
  min-height: 100%;
  background-color: #121212;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  color:#f2f2f2;
  grid-gap:0px 50px;
  font-family: 'Roboto';
}
.contact .left {
  text-align: right;
}
.contact p {
  position: relative;
  top:50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.contact>.right {
  text-align: left;
  font-size: 10em;
  font-weight: bold;
}

.email {
  cursor:text;
}
@media ( max-width: 768px ) {
  .workplace {
    display: none;
  }
}


.navi {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  top:0;
  left:0;
  position: fixed;
  font-size: .6em;
  mix-blend-mode: difference;
  z-index:3;
}
.navi .right {
  margin:0 auto;
  grid-column-start: 2;
}
.navi .right li {
  color:white;
  cursor: pointer;
  margin-right: 50px;
}

ul {
  cursor: default;
  list-style: none;
  padding:0;
}
li {
  font-family: 'Source Code Pro', monospace;
   font-size:2.5em;
   margin-top: 20px;
   transition: -2px;
   display: inline-block;
   -webkit-transition: .5s;
   transition: .5s;
}

.after {
  background-color: #121212;
  width: 100%;
  height: 100%;
  color:#f2f2f2;
}
.fix {
  position: fixed;
  z-index:0;
}
.about_cvs {
font-family: 'Roboto', sans-serif;
  background-color: #121212;
  width: 100%;
  min-width: 450px;
  min-height: 100%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
}
.about_cvs .left {
text-align: right;
background-clip:padding-box;
}
.about_cvs .left h1:first-child {
-webkit-animation: l_move .3s .5s linear forwards;
        animation: l_move .3s .5s linear forwards;
}
.about_cvs .left h1:nth-child(2) {
-webkit-animation: r_move .3s .5s linear forwards;
        animation: r_move .3s .5s linear forwards;
}
.about_cvs .left h1:nth-child(3) {
-webkit-animation: l_move .3s .5s linear forwards;
        animation: l_move .3s .5s linear forwards;
}
.about_cvs .left h1:last-child {
-webkit-animation: r_move .3s 1.7s linear forwards;
        animation: r_move .3s 1.7s linear forwards;
-webkit-transition: .3s;
transition: .3s;
}
.about_cvs .left h1 {
  opacity:0;
}
.about_cvs .right {
  display: grid;
  grid-template-columns: repeat(2,1fr);
}
.about_cvs .right h1:first-child {
  margin-left: 0;
  /* animation: fadein .3s 2.5s linear forwards; */
}
.about_cvs .right h1:nth-child(2) {
  -webkit-animation: l_move .3s 1.7s linear forwards;
          animation: l_move .3s 1.7s linear forwards;
}
.about_cvs .right h1:last-child {
  display: inline;
  -webkit-transition: .3s;
  transition: .3s;
  /* animation: fadein .3s 2.5s linear forwards; */
}
.about_cvs .right .text{
  position: relative;
}
.about_cvs .right h1{
  opacity:0;
  margin-left:50px;
}
.text p {
  line-height: 10px;
}
.about_cvs h1 {
  line-height: 70%;
  font-size:20vh;
  font-weight: bold;
}
.talk p {
  opacity: 0;
}
.hover {
  color:#4374D9;
}

.visible {
  opacity:1 !important;
}
.fadein {
  -webkit-animation: fadein .3s linear forwards;
          animation: fadein .3s linear forwards;
}
.fadeout {
  -webkit-animation: fadeout .3s linear forwards;
          animation: fadeout .3s linear forwards;
}
.info {
  color:#ededed;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: 1fr 2fr;
  grid-template-areas:
  "hi hi"
  "left right";
  position: relative;
  z-index:2;
  width: 100%;
  height: 100%;
  background-color: #121212;
  font-family: 'Roboto','Noto Sans KR', sans-serif;
  grid-gap: 0px 50px;
}
.info p {
  line-height: 15px;
}
.info .hi {
  grid-area:hi;
  opacity:0;
  padding-top:100px;
  text-align: center;
  font-size: 4em;
  font-weight: bold;
}
.info .left {
  text-align: right;
  font-size: 14em;
  grid-area:left;
  opacity:0;
}
.info .right {
  grid-area:right;
}
.info .right .talk {
  margin-top: 70px;
}
@-webkit-keyframes r_move {
  0%{
    opacity: 0;
    -webkit-transform:translateX(-100px);
            transform:translateX(-100px);
  }

  100%{
    opacity:1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes r_move {
  0%{
    opacity: 0;
    -webkit-transform:translateX(-100px);
            transform:translateX(-100px);
  }

  100%{
    opacity:1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@-webkit-keyframes l_move {
  0%{
    opacity: 0;
    -webkit-transform:translateX(100px);
            transform:translateX(100px);
  }

  100%{
    opacity:1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes l_move {
  0%{
    opacity: 0;
    -webkit-transform:translateX(100px);
            transform:translateX(100px);
  }

  100%{
    opacity:1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@-webkit-keyframes fadein {
  0%{
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
@keyframes fadein {
  0%{
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
@-webkit-keyframes l_fadein {
  0%{
    opacity: 0;
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes l_fadein {
  0%{
    opacity: 0;
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@-webkit-keyframes fadeout {
  0%{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100%{
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
@keyframes fadeout {
  0%{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100%{
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
.hide {
  opacity:0;
}
a {
  padding-top: 80px;
}
a span {
  position: absolute;
  bottom: 52px;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-animation: sdb 2s infinite;
  animation: sdb 2s infinite;
  opacity: 0;
  box-sizing: border-box;
  z-index: 3;
}
a span:nth-of-type(1) {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
a span:nth-of-type(2) {
  bottom: 36px;
  -webkit-animation-delay: .15s;
  animation-delay: .15s;
}
a  span:nth-of-type(3) {
  bottom: 20px;
  -webkit-animation-delay: .3s;
  animation-delay: .3s;
}
@-webkit-keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@media ( max-width: 768px ) {
  .after {
    display: none;
  }
}

