@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
.work_cvs {
  position: absolute;
  background-color: #121212;
  height: 100%;
  width: 100%;
  margin:0;
  padding:0;
}
.work_list {
  position: relative;
  top:50%;
  transform: translateY(-50%);
}
.work_list li {
  color:  #f2f2f2;
  opacity:.5;
  font-family: 'Roboto';
  font-size: 5vw;
  margin-left: 50px;
  transition: .2s
}
.work_list li:hover {
  opacity: 1;
  margin-left: 70px;
}
.work_list ul {
  margin:0;
  padding:0;
}
@media ( max-width: 768px ) {
  .workplace {
    display: none;
  }
}
