@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap');
.after {
  background-color: #121212;
  width: 100%;
  height: 100%;
  color:#f2f2f2;
}
.fix {
  position: fixed;
  z-index:0;
}
.about_cvs {
font-family: 'Roboto', sans-serif;
  background-color: #121212;
  width: 100%;
  min-width: 450px;
  min-height: 100%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
}
.about_cvs .left {
text-align: right;
background-clip:padding-box;
}
.about_cvs .left h1:first-child {
animation: l_move .3s .5s linear forwards;
}
.about_cvs .left h1:nth-child(2) {
animation: r_move .3s .5s linear forwards;
}
.about_cvs .left h1:nth-child(3) {
animation: l_move .3s .5s linear forwards;
}
.about_cvs .left h1:last-child {
animation: r_move .3s 1.7s linear forwards;
transition: .3s;
}
.about_cvs .left h1 {
  opacity:0;
}
.about_cvs .right {
  display: grid;
  grid-template-columns: repeat(2,1fr);
}
.about_cvs .right h1:first-child {
  margin-left: 0;
  /* animation: fadein .3s 2.5s linear forwards; */
}
.about_cvs .right h1:nth-child(2) {
  animation: l_move .3s 1.7s linear forwards;
}
.about_cvs .right h1:last-child {
  display: inline;
  transition: .3s;
  /* animation: fadein .3s 2.5s linear forwards; */
}
.about_cvs .right .text{
  position: relative;
}
.about_cvs .right h1{
  opacity:0;
  margin-left:50px;
}
.text p {
  line-height: 10px;
}
.about_cvs h1 {
  line-height: 70%;
  font-size:20vh;
  font-weight: bold;
}
.talk p {
  opacity: 0;
}
.hover {
  color:#4374D9;
}

.visible {
  opacity:1 !important;
}
.fadein {
  animation: fadein .3s linear forwards;
}
.fadeout {
  animation: fadeout .3s linear forwards;
}
.info {
  color:#ededed;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: 1fr 2fr;
  grid-template-areas:
  "hi hi"
  "left right";
  position: relative;
  z-index:2;
  width: 100%;
  height: 100%;
  background-color: #121212;
  font-family: 'Roboto','Noto Sans KR', sans-serif;
  grid-gap: 0px 50px;
}
.info p {
  line-height: 15px;
}
.info .hi {
  grid-area:hi;
  opacity:0;
  padding-top:100px;
  text-align: center;
  font-size: 4em;
  font-weight: bold;
}
.info .left {
  text-align: right;
  font-size: 14em;
  grid-area:left;
  opacity:0;
}
.info .right {
  grid-area:right;
}
.info .right .talk {
  margin-top: 70px;
}
@keyframes r_move {
  0%{
    opacity: 0;
    transform:translateX(-100px);
  }

  100%{
    opacity:1;
    transform: translateX(0px);
  }
}
@keyframes l_move {
  0%{
    opacity: 0;
    transform:translateX(100px);
  }

  100%{
    opacity:1;
    transform: translateX(0px);
  }
}
@keyframes fadein {
  0%{
    opacity: 0;
    transform: translateY(50px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes l_fadein {
  0%{
    opacity: 0;
    transform: translateX(-30px);
  }
  100%{
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes fadeout {
  0%{
    opacity: 1;
    transform: translateY(0px);
  }
  100%{
    opacity: 0;
    transform: translateY(-10px);
  }
}
.hide {
  opacity:0;
}
a {
  padding-top: 80px;
}
a span {
  position: absolute;
  bottom: 52px;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-animation: sdb 2s infinite;
  animation: sdb 2s infinite;
  opacity: 0;
  box-sizing: border-box;
  z-index: 3;
}
a span:nth-of-type(1) {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
a span:nth-of-type(2) {
  bottom: 36px;
  -webkit-animation-delay: .15s;
  animation-delay: .15s;
}
a  span:nth-of-type(3) {
  bottom: 20px;
  -webkit-animation-delay: .3s;
  animation-delay: .3s;
}
@-webkit-keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes sdb {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@media ( max-width: 768px ) {
  .after {
    display: none;
  }
}
