.contact {
  width: 100%;
  min-height: 100%;
  background-color: #121212;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  color:#f2f2f2;
  grid-gap:0px 50px;
  font-family: 'Roboto';
}
.contact .left {
  text-align: right;
}
.contact p {
  position: relative;
  top:50%;
  transform: translateY(-50%);
}
.contact>.right {
  text-align: left;
  font-size: 10em;
  font-weight: bold;
}

.email {
  cursor:text;
}
@media ( max-width: 768px ) {
  .workplace {
    display: none;
  }
}
