
.navi {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  top:0;
  left:0;
  position: fixed;
  font-size: .6em;
  mix-blend-mode: difference;
  z-index:3;
}
.navi .right {
  margin:0 auto;
  grid-column-start: 2;
}
.navi .right li {
  color:white;
  cursor: pointer;
  margin-right: 50px;
}

ul {
  cursor: default;
  list-style: none;
  padding:0;
}
li {
  font-family: 'Source Code Pro', monospace;
   font-size:2.5em;
   margin-top: 20px;
   transition: -2px;
   display: inline-block;
   transition: .5s;
}
